(function () {

///////////////////////////////////////////////////////////////////////
//                                                                   //
// packages/gwendall:auth-client-callbacks/client/lib.js             //
//                                                                   //
///////////////////////////////////////////////////////////////////////
                                                                     //
Accounts._hooksLogin = Accounts._hooksLogin || [];                   // 1
Accounts._hooksLogout = Accounts._hooksLogout || [];                 // 2
                                                                     // 3
Accounts.onLogin = function(cb) {                                    // 4
  if (!_.isFunction(cb)) return;                                     // 5
  Accounts._hooksLogin.push(cb);                                     // 6
}                                                                    // 7
                                                                     // 8
Accounts.onLogout = function(cb) {                                   // 9
  if (!_.isFunction(cb)) return;                                     // 10
  Accounts._hooksLogout.push(cb);                                    // 11
}                                                                    // 12
                                                                     // 13
Accounts._callHooksLogin = function() {                              // 14
  var self = this;                                                   // 15
  Accounts._hooksLogin.forEach(function(cb) {                        // 16
    cb.apply(self);                                                  // 17
  });                                                                // 18
}                                                                    // 19
                                                                     // 20
Accounts._callHooksLogout = function() {                             // 21
  var self = this;                                                   // 22
  Accounts._hooksLogout.forEach(function(cb) {                       // 23
    cb.apply(self);                                                  // 24
  });                                                                // 25
}                                                                    // 26
                                                                     // 27
Meteor.autorun(function() {                                          // 28
  if (Meteor.userId()) {                                             // 29
    Accounts._callHooksLogin();                                      // 30
  } else {                                                           // 31
    Accounts._callHooksLogout();                                     // 32
  }                                                                  // 33
});                                                                  // 34
                                                                     // 35
///////////////////////////////////////////////////////////////////////

}).call(this);
